<template>
  <div :class="[className, 'echarts']" ref="dom"></div>
</template>

<script>
import { watch, onMounted, ref } from "vue";
import * as Echarts from "echarts";
import { v4 as uuidv4 } from "uuid";

export default {
  name: "VueEcharts",
  props: {
    options: Object,
    theme: [String, Object],
  },
  setup(ctx) {
    let dom = ref();
    let chart = ref();
    let className = `echarts-${uuidv4()}`;
    const initChart = () => {
      if (!chart.value && dom.value) {
        // dom.value = document.getElementsByClassName(className)[0];
        if (ctx.theme) {
          chart.value = Echarts.init(dom.value, ctx.theme);
        } else {
          chart.value = Echarts.init(dom.value);
        }
      }
      if (ctx.options) {
        chart.value.setOption(ctx.options);
      }
    };
    onMounted(() => {
      initChart();
    });
    watch(
      ctx.options,
      (newValue, oldValue) => {
        initChart();
      },
      true
    );
    return {
      dom,
      chart,
      className,
    };
  },
};
</script>

<style lang="scss" scoped>
.echarts {
  width: 100%;
  height: 100%;
}
</style>
