<template>
  <count-to :startVal="prevData" :endVal="data" />
</template>

<script>
import { ref, watch } from "vue";

export default {
  props: {
    data: {
      type: Number,
      default: null,
    },
  },
  setup(props) {
    const prevData = ref(0);
    watch(
      () => props.data,
      (nextValue, prevValue) => {
        prevData.value = prevValue;
      }
    );

    return {
      prevData,
    };
  },
};
</script>