import { createRouter, createWebHashHistory } from 'vue-router'

const routes = [
  {
    path: '/',
    name: 'Home',
    redirect: '/screen2'
  },
  {
    path: '/screen1',
    name: 'Screen1',
    component: () => import(/* webpackChunkName: "screen1" */ '@/views/Home/Screen1.vue')
  },
  {
    path: '/screen2',
    name: 'Screen2',
    component: () => import(/* webpackChunkName: "screen2" */ '@/views/Home/Screen2.vue')
  },
  {
    path: '/screen3',
    name: 'Screen3',
    component: () => import(/* webpackChunkName: "screen3" */ '@/views/Home/Screen3.vue')
  },
  // {
  //   path: '/about',
  //   name: 'About',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
