import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import container from '@/components/Container/Container.vue'
import countTo from '@/components/VueCountTo/vue-countTo.vue'
import MyCountTo from '@/components/project/MyCountTo/Index.vue'
import ECharts from '@/components/VueEcharts/VueEcharts.vue'

import '@/styles/common.scss'

createApp(App)
    .use(store)
    .use(router)
    .component('container', container)
    .component('count-to', countTo)
    .component('my-count-to', MyCountTo)
    .component('echarts', ECharts)
    .mount('#app')
